
export function simpleRoof(material) {
    // Beton, Træ, Metal, Skifer, Tagpap, Tegl
    if (material === 'Beton') {
        const Materialer = [0.8, 0.1, 0.1, 0, 0, 0]
        return Materialer
    } else if (material === 'Skifer') {
        const Materialer = [0, 0.1, 0.1, 0.8, 0, 0]
        return Materialer
    } else if (material === 'Tagpap') {
        const Materialer = [0, 0.5, 0.1, 0, 0.2, 0]
        return Materialer
    } else if (material === 'Tegl') {
        const Materialer = [0, 0.1, 0.1, 0, 0, 0.8]
        return Materialer
    } else {
        return []
    }
} 

export function simpleWall(material) {
    // Beton, Isolering, Træ, Mursten, Metal
    if (material === 'Beton') {
        const Materialer = [0.3, 0.5, 0.2, 0, 0]
        return Materialer
    } else if (material === 'Mursten') {
        const Materialer = [0, 0.5, 0.2, 0.3, 0]
        return Materialer
    } else if (material === 'Træ') {
        const Materialer = [0, 0.8, 0.1, 0, 0.1]
        return Materialer
    } else {
        return []
    }
} 

