import React from 'react';
import './SearchBar.css';
import { ReactSearchAutocomplete } from 'react-search-autocomplete'
import items from '../../items/items'
import BuildingView from '../BuildingView/BuildingView'

class SearchBar extends React.Component {
  constructor(props) {
    super(props);

    this.handleOnSearch = this.handleOnSearch.bind(this);
    this.handleOnSelect = this.handleOnSelect.bind(this);
    this.handleOnFocus = this.handleOnFocus.bind(this);
  }

  handleOnSearch = (string, results) => {
    console.log(string, results);
  }

  handleOnSelect = (item) => {
    console.log(item);
    const address = item.name;
    const area = item.area;
    const imageSrc = item.imageSrc;
    const floors = item.floors;
    const built = item.built;
    const selectAddress = true;
    const location = item.location;
    const goBack = false;
    this.props.onChange(address,area,imageSrc,floors,built,selectAddress,location,goBack)
  }

  handleOnFocus = () => {
    console.log("Focused");
  }

  render() {
    if (this.props.building.goBack === true) {
    return (
      <div className='SearchPage'>
        <div className="SearchBar">
          <header className="SearchHeader">
            <div className="SearchBar-fields" style={{ width: 200, margin: 20 }}>
              <ReactSearchAutocomplete
              placeholder="Adresse" 
              items = {items}
              onSearch={this.handleOnSearch}
              onSelect={this.handleOnSelect}
              onFocus={this.handleOnFocus}
              styling={{ zIndex: 2 }} // To display it on top of the search box below
              autoFocus
            />
            </div>
          </header>
        </div>
      </div>
      
    );
    } else {
      return(
        <div></div>
      )
    }
  }
}

export default SearchBar;